import {withStyles} from "@material-ui/core/styles";
import TextField from "@material-ui/core/TextField";
import React from "react";

const TextFieldMedium = withStyles((theme) => ({
  //////       
    root: {
    "& input:valid + fieldset": {
      // 
      borderColor: global.config.colors.NotActiveElement,
      borderWidth: 2,
    },
    "& input:invalid + fieldset": {
      //  
      borderColor: global.config.colors.red,
      borderWidth: 2,
    },
    "& input:valid:focus + fieldset": {
      //
      borderLeftWidth: 6,
      padding: "4px !important", //  
      borderColor: global.config.colors.purple,
    },
    "& label.Mui-focused + div fieldset": {
      //    
      borderColor: global.config.colors.purple + "!important",
    },
    "& label.Mui-focused": {
      //  
      color: global.config.colors.red,
    },
    "& label": {
      // 
      color: global.config.colors.purple,
    },
  },
}))((props) => <TextField {...props} />);

export default TextFieldMedium;